import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { NgForm, NgModel, NgControl, FormsModule } from '@angular/forms';
import { LoopBackAuth, BooksApi, ContainerApi ,CityApi,Type_of_housing_masterApi} from '../../../../shared/loopback-sdk';
import { CookieService } from 'angular2-cookie/core';
import { Headers, Http, Response } from '@angular/http';
import { stringify } from 'querystring';
import { ShareService } from '../../../../providers/share-service/share-service';
import { encode } from 'punycode';
declare var window: any;
declare var hostname: any;
declare var sdkPrefix;
@Component({
	templateUrl: 'add-type-of-housing.component.html'
})
export class AddTypeOfHousingComponent {
	@ViewChild('typOfHousngName') name: NgModel;
	display: boolean = false; result: any;
	submitted: boolean = false;
	typOfHousngName: any;
	busy: any;
	userDetails: any;
	rolename: any;
	loading: boolean = false;
	data: any;
	perPageLimit:number = 10;
	offset:number=0;
	total:number;
	typOfHusngStatus:boolean = false;

	constructor(private loopAuth: LoopBackAuth, private http: Http, private _cookieService: CookieService, private books: BooksApi, private router: Router, private containerApi: ContainerApi, private share: ShareService,private cityApi:CityApi,private typeOfHousingApi:Type_of_housing_masterApi) {
		this.userDetails = this.loopAuth.getCurrentUserData();
		
		this.rolename = this.userDetails.userDet.roles;
	}


	async submit(form) {
		this.submitted = true;
		let validFlag = true;
		if (form.valid) {

			//function call to change base64 format data to Image
			var dataArrObj = {
				"type_of_housing": this.capital_letter(this.typOfHousngName),
				//"status": (this.typOfHusngStatus)?true:false
				"status":false
			}

			this.loading = true;
			this.ifNameExist(dataArrObj).then((total)=>{
				if(total > 0){
					this.loading = false;
					this.result = "Type of housing name already exist";
					this.display = true;
				}else{
					this.typeOfHousingApi.upsert(dataArrObj).subscribe((data)=>{
						this.loading = false;
						this.result = "success";
						this.display = true;
					},(err)=>{
						this.loading = false;
						this.result = "Something went wrong";
						this.display = true;
					});
				}
			},(err)=>{
				this.loading = false;
						this.result = "Something went wrong";
						this.display = true;
			});

			
		}
	}

	ifNameExist(dataObj){
		return new Promise((resolve,reject)=>{
			try{

				this.typeOfHousingApi.getTypeOfHousingStageCountName({ "type_of_housing": dataObj.type_of_housing}).subscribe((data)=>{
					resolve(data.result[0].total);
				},(err)=>{
					reject(err);
				});

			}catch(err){
				reject(err);
			}
		});
		
		
	}

	ngOnInit(): void {

	}

	capital_letter(str) 
	{
		if(str != '' && str != null && str != undefined){
			str = str.split(" ");

			for (var i = 0, x = str.length; i < x; i++) {
				str[i] = str[i][0].toUpperCase() + str[i].substr(1);
			}
	
			return str.join(" ");
		}else{
			return str
		}
		
	}

	goToListing() {
		// window.location.reload();
		this.router.navigate(["type-of-housing"]);
	}

	
}


