import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AddTypeOfHousingComponent } from './add-type-of-housing.component';

const routes: Routes = [
  {
    path: '',
    component: AddTypeOfHousingComponent,
    data: {
      title: 'Add Type of Housing'
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TypeOfHousingCrudRoutingModule {}



