import { NgModule } from '@angular/core';
import { CommonModule }      from '@angular/common';
import { FormsModule } from "@angular/forms";
import { HttpModule } from "@angular/http";
import { AddTypeOfHousingComponent } from './add-type-of-housing.component';
import { TypeOfHousingCrudRoutingModule } from './add-type-of-housing-routing.module';
import { DialogModule } from 'primeng/primeng';
import {BusyModule} from 'angular2-busy';
import {EditorModule} from 'primeng/primeng';

@NgModule({
  imports: [
    CommonModule, 
    FormsModule,
    HttpModule,
    TypeOfHousingCrudRoutingModule,
    DialogModule,
    EditorModule,
	  BusyModule
  ],
  declarations: [
    AddTypeOfHousingComponent
  ]
})
export class AddTypeOfHousingModule { }



